"use client";

import { useState } from "react";
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  Tooltip,
  ResponsiveContainer,
} from "recharts";

import { Slider } from "@/components/ui/slider";
import { Switch } from "@/components/ui/switch";
import { ModeToggle } from "@/components/ui/mode-toggle";
import {
  Select,
  SelectTrigger,
  SelectValue,
  SelectContent,
  SelectItem,
} from "@/components/ui/select";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";

type TimelinePoint = { time: string; soc: number };

type PredictResponse = {
  hours: number;
  final_battery: number;
  timeline: TimelinePoint[];
  start_time: string;
  end_time: string;
  cost_now: number;
  cost_optimized: number;
  savings: number;
  error?: string;
};

export default function EVOptimizerPage() {
  const [battery, setBattery] = useState<number>(25);
  const [power, setPower] = useState<number>(7);
  const [departure, setDeparture] = useState<string>("19:00");
  const [cheapMode, setCheapMode] = useState<boolean>(true);

  const [result, setResult] = useState<PredictResponse | null>(null);
  const [loading, setLoading] = useState<boolean>(false);
  const [countdown, setCountdown] = useState<string>("");

  const calculateCountdown = (time: string) => {
    const now = new Date();
    const [h, m] = time.split(":").map(Number);
    const target = new Date();
    target.setHours(h, m, 0, 0);
    if (target <= now) target.setDate(target.getDate() + 1);
    const diff = target.getTime() - now.getTime();
    const hrs = Math.floor(diff / (3600 * 1000));
    const mins = Math.floor((diff % (3600 * 1000)) / 60000);
    return `${hrs}h ${mins}m`;
  };

  const handleOptimize = async () => {
    setLoading(true);
    setResult(null);
    setCountdown(calculateCountdown(departure));

    try {
      const res = await fetch("http://localhost:8000/predict", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          battery_level: battery,
          charger_power: power,
          target_time: departure,
          cheap_mode: cheapMode,
        }),
      });

      const data: PredictResponse = await res.json();
      if (!res.ok) throw new Error(data.error || "Request failed");
      setResult(data);
    } catch (err) {
      alert("‚ö†Ô∏è Error connecting to backend!");
    } finally {
      setLoading(false);
    }
  };

  return (
    <main className="min-h-screen bg-background text-foreground p-6">
      {/* Header with Mode Toggle */}
      <div className="flex justify-between items-center mb-8">
        <h1 className="text-3xl font-bold">
          ‚ö° Smart EV Charging Optimization
        </h1>
        <ModeToggle />
      </div>

      <div className="max-w-5xl mx-auto grid grid-cols-1 md:grid-cols-2 gap-6">
        {/* ‚öôÔ∏è Controls */}
        <Card>
          <CardHeader>
            <CardTitle>Charging Settings</CardTitle>
          </CardHeader>
          <CardContent className="space-y-4">
            {/* Battery */}
            <div>
              <label className="block text-sm mb-2 text-muted-foreground">
                Current Battery Level:{" "}
                <span className="font-semibold text-foreground">{battery}%</span>
              </label>
              <Slider
                value={[battery]}
                min={0}
                max={100}
                step={1}
                onValueChange={(v) => setBattery(v[0])}
              />
            </div>

            {/* Power */}
            <div>
              <label className="block text-sm mb-2 text-muted-foreground">
                Charger Power (kW)
              </label>
              <Select
                value={power.toString()}
                onValueChange={(v) => setPower(Number(v))}
              >
                <SelectTrigger>
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="3">3 kW</SelectItem>
                  <SelectItem value="7">7 kW</SelectItem>
                  <SelectItem value="11">11 kW</SelectItem>
                  <SelectItem value="22">22 kW</SelectItem>
                </SelectContent>
              </Select>
            </div>

            {/* Time */}
            <div>
              <label className="block text-sm mb-2 text-muted-foreground">
                Departure Time
              </label>
              <input
                type="time"
                value={departure}
                onChange={(e) => setDeparture(e.target.value)}
                className="w-full p-2 rounded-md bg-secondary"
              />
            </div>

            {/* Cheap mode */}
            <div className="flex items-center justify-between">
              <span className="text-sm text-muted-foreground">
                Optimize by electricity cost
              </span>
              <Switch checked={cheapMode} onCheckedChange={setCheapMode} />
            </div>

            {/* Button */}
            <Button
              onClick={handleOptimize}
              disabled={loading}
              className="w-full"
            >
              {loading ? "‚è≥ Optimizing..." : "üî• Optimize Charging"}
            </Button>

            <p className="text-xs text-muted-foreground">
              Low-price hours: 22:00‚Äì02:00 daily (Malaysia ToU)
            </p>
          </CardContent>
        </Card>

        {/* üìà Results */}
        <Card>
          <CardHeader>
            <CardTitle>Recommendation</CardTitle>
          </CardHeader>

          <CardContent>
            {!result && (
              <p className="text-muted-foreground">
                No result yet ‚Äî adjust values and press Optimize.
              </p>
            )}

            {result && (
              <>
                <div className="space-y-2 mb-4">
                  <div className="flex justify-between">
                    <span>‚è± Countdown</span>
                    <span className="font-semibold">{countdown}</span>
                  </div>
                  <div className="flex justify-between">
                    <span>üöó Charging Needed</span>
                    <span className="font-semibold">{result.hours}h</span>
                  </div>
                  <div className="flex justify-between">
                    <span>üîã Final Battery</span>
                    <span className="font-semibold">
                      {result.final_battery}%
                    </span>
                  </div>
                  <div className="flex justify-between">
                    <span>üí∞ Cost (now)</span>
                    <span>RM {result.cost_now.toFixed(2)}</span>
                  </div>
                  <div className="flex justify-between">
                    <span>üí∏ Cost (optimized)</span>
                    <span>RM {result.cost_optimized.toFixed(2)}</span>
                  </div>
                  <div className="flex justify-between">
                    <span>üéØ Savings</span>
                    <span className="text-green-500 font-semibold">
                      RM {result.savings.toFixed(2)}
                    </span>
                  </div>
                </div>

                {/* Graph */}
                <div className="h-56">
                  <ResponsiveContainer width="100%" height="100%">
                    <LineChart data={result.timeline}>
                      <XAxis dataKey="time" />
                      <YAxis domain={[0, 100]} />
                      <Tooltip />
                      <Line
                        type="monotone"
                        dataKey="soc"
                        stroke="currentColor"
                        strokeWidth={2}
                      />
                    </LineChart>
                  </ResponsiveContainer>
                </div>

                <p className="text-xs text-muted-foreground mt-2">
                  Start: {result.start_time} ‚Äî End: {result.end_time}
                </p>
              </>
            )}
          </CardContent>
        </Card>
      </div>
    </main>
  );
}
